import { u } from 'umbrellajs/umbrella';
import BaseComponent from '~global-js/component/base-component';
import AutoCompleteComponent from '~components/autocomplete/js/component';
import * as actions from '~components/autocomplete/js/actions';

export default class AutoCompleteModalWithDatePickerComponent extends BaseComponent {
  constructor(element, props = {}) {
    super(element, props);

    this.modal = document.querySelector('.autocomplete__modal--datepicker');
    this.modalCloseButton = this.modal.querySelector('.autocomplete__modal__cancel-btn');
    this.modalInput = this.modal.querySelector('.autocomplete__modal__input');
    this.component = null;
    this.shelter = null;
    this.isInputElement = element.tagName === 'INPUT';
  }

  init() {
    if (this.isInputElement) {
      this.activeShelter();
    }

    this.addEventListener();
    this.props.store.dispatch(actions.registerComponent('modalInstance'));
    this.props.store.dispatch(actions.componentMounted('modalInstance'));
  }

  activeShelter = () => {
    this.shelter = u('<div>').addClass('autocomplete__modal__shelter');
    u(this.element).after(this.shelter);
  };

  addEventListener = () => {
    if (this.isInputElement) {
      this.shelter.on('click', this.activeModal);
    } else {
      this.element.addEventListener('click', this.activeModal);
    }
  };

  activeModal = () => {
    document.documentElement.classList.add('no-scroll');
    this.modal.classList.add('autocomplete__modal--active');
    this.modalCloseButton.addEventListener('click', this.destroyModal);
    this.modalInput.placeholder = this.props.placeholder;

    if (this.isInputElement && this.element.value) {
      this.modalInput.value = this.element.value;
    }

    this.component = new AutoCompleteComponent(this.modalInput, 'modalWithDatePicker', Object.assign({}, this.props, {
      suggestionBoxClass: null,
      clearButtonStyle: {
        top: '0.875rem',
        padding: '1rem',
      },
    }));
    this.component.init();
    this.modalInput.focus();
  };

  destroyModal = () => {
    document.documentElement.classList.remove('no-scroll');
    this.modal.classList.remove('autocomplete__modal--active');

    this.component.destroy();
    this.modalCloseButton.removeEventListener('click', this.destroyModal);
    this.modalInput.value = '';
    this.modalInput.placeholder = '';
    this.modalInput.blur();
  };

  destroy() {
    if (this.isInputElement) {
      this.shelter.off('click', this.activeModal);
      this.shelter.remove();
    } else {
      this.element.removeEventListener('click', this.activeModal);
    }
  }

  static defaultProps = {
    placeholder: '',
  }
}



