import { createStore, applyMiddleware, combineReducers } from 'redux';
import thunk from 'redux-thunk';
import autoSuggest from '~components/autocomplete/js/reducers/autoSuggest';
import preSuggest from '~components/autocomplete/js/reducers/preSuggest';
import datePickerReducer from '~components/date-picker/js/reducers';
// import createLogger from 'redux-logger';

const middlewares = [thunk];

const store = createStore(
  combineReducers({
    autoSuggest,
    preSuggest,
    datePicker: datePickerReducer,
  }),
  applyMiddleware(...middlewares)
);

export default store;



