import axios from '~global-js/helpers/axios';
import document from 'global/document';
import Enum from '~global-js/helpers/enum';
import { getLocale } from '~global-js/helpers/locale';
import { getItem } from '~global-js/helpers/storage';
import { localStorageKeys, localStorageVersion, asyncStateTypes } from '~components/autocomplete/js/constants';
import { event } from '~global-js/helpers/tracking';
import getEnvironment, { environments } from '~global-js/helpers/environment';

export const actionTypes = new Enum(
  'AUTOCOMPLETE_REGISTER_COMPONENT',
  'AUTOCOMPLETE_DESTROY_COMPONENT',
  'AUTOCOMPLETE_COMPONENT_MOUNTED',
  'AUTOCOMPLETE_OPEN_SUGGESTION_BOX',
  'AUTOCOMPLETE_HIDE_SUGGESTION_BOX',
  'AUTOCOMPLETE_FETCHING_DATA',
  'AUTOCOMPLETE_FETCHING_FAILED',
  'AUTOCOMPLETE_SET_AUTO_SUGGEST_DATA',
  'AUTOCOMPLETE_RESET_AUTO_SUGGEST_DATA',
  'AUTOCOMPLETE_SET_POPULAR_SEARCH_DATA',
  'AUTOCOMPLETE_SET_POPULAR_CITIES_DATA',
  'AUTOCOMPLETE_SET_RECENT_SEARCH_DATA',
  'AUTOCOMPLETE_UPDATE_RECENT_SEARCH_DATA',
  'AUTOCOMPLETE_UPDATE_DATE_PICKER',
);

const localStorageSessionTimeOut = 7200000;


let cancelTokenSource = null;

// temporary function, will remove after platform api available
function buildLocale() {
  const locale = getLocale();
  let host = '';

  if (document.location && document.location.hostname.indexOf('partner') !== -1) {
    const environment = getEnvironment();
    if (environment === environments.PROD) {
      host = locale === 'zh-cn' ? '//cn.student.com' : '//www.student.com';
    } else if (environment === environments.STAGE) {
      host = locale === 'zh-cn' ? '//hurricane-cn.dandythrust.com' : '//hurricane-www.dandythrust.com';
    }
  }

  if (locale === 'en-us' || locale === 'zh-cn') {
    return host;
  }

  return `${host}/${locale}`;
}

export function registerComponent(id) {
  return {
    type: actionTypes.AUTOCOMPLETE_REGISTER_COMPONENT,
    id,
  };
}

export function destroyComponent(id) {
  return {
    type: actionTypes.AUTOCOMPLETE_DESTROY_COMPONENT,
    id,
  };
}

export function getPopularSearchData() {
  const localData = getItem(localStorageKeys.popularSearch);

  if (localData && localData.locale === getLocale()
      && localData.version === localStorageVersion
      && new Date().getTime() - localData.createTime < localStorageSessionTimeOut) {
    return (dispatch) => {
      dispatch(setPopularSearchData(localData.payload, true));
    };
  }

  return (dispatch) => {
    axios.get(`${buildLocale()}/popular-search`).then((response) => {
      dispatch(setPopularSearchData(response.data));
    });
  };
}

export function setPopularSearchData(data, fromCache = false) {
  return {
    type: actionTypes.AUTOCOMPLETE_SET_POPULAR_SEARCH_DATA,
    payload: data,
    fromCache,
  };
}

export function getPopularCitiesData() {
  const localData = getItem(localStorageKeys.popularCities);

  if (localData && localData.locale === getLocale()
      && localData.version === localStorageVersion
      && new Date().getTime() - localData.createTime < localStorageSessionTimeOut) {
    return (dispatch) => {
      dispatch(setPopularCitiesData(localData.payload, true));
    };
  }

  return (dispatch) => {
    axios.get(`${buildLocale()}/popular-cities`).then((response) => {
      dispatch(setPopularCitiesData(response.data));
    });
  };
}

export function setPopularCitiesData(data, fromCache = false) {
  return {
    type: actionTypes.AUTOCOMPLETE_SET_POPULAR_CITIES_DATA,
    payload: data,
    fromCache,
  };
}

export function getRecentSearchData() {
  const data = getItem(localStorageKeys.recentSearch);

  if (data && data.locale === getLocale() && data.version === localStorageVersion) {
    return {
      type: actionTypes.AUTOCOMPLETE_SET_RECENT_SEARCH_DATA,
      payload: data.payload,
      reset: false,
    };
  }

  return {
    type: actionTypes.AUTOCOMPLETE_SET_RECENT_SEARCH_DATA,
    payload: {},
    reset: true,
  };
}

export function updateRecentSearchData(entity) {
  return {
    type: actionTypes.AUTOCOMPLETE_UPDATE_RECENT_SEARCH_DATA,
    payload: entity,
  };
}

export function resetSearchData(id) {
  return {
    type: actionTypes.AUTOCOMPLETE_RESET_AUTO_SUGGEST_DATA,
    id,
  };
}

export function fetchSeachData(searchTerm, props, id, sendEvent = true) {
  return (dispatch, getState) => {
    const fetchingState = getState().autoSuggest[id].status;

    if (fetchingState === asyncStateTypes.FETCHING) {
      cancelTokenSource.cancel('Operation canceled by the user.');
    }

    if (searchTerm === '') {
      dispatch(resetSearchData(id));
    } else {
      cancelTokenSource = axios.CancelToken.source();
      dispatch(settingFetchingStatus(id));

      axios.get(`${buildLocale()}/autocomplete`, {
        cancelToken: cancelTokenSource.token,
        params: {
          term: searchTerm.toLowerCase(),
          type: props.searchType,
          limit: 10,
        },
      }).then((response) => {
        if (sendEvent) {
          event({
            c: props.searchPlacement,
            a: 'view',
            l: `search > enterKeywords:${searchTerm}`,
            mp: [{
              type: 'track',
              key: 'Entered Search Keyword',
              params: {
                'Search Query': searchTerm,
                'Empty Search': Object.keys(response.data).length === 0,
                'Search Placement': props.searchPlacement,
              },
            }],
          });
        }
        dispatch(setSearchData(response.data, id));
      }).catch((error) => {
        if (axios.isCancel(error)) {

        } else {
          dispatch(settingFetchingFailed(error, id));
        }
      });
    }
  };
}

export function settingFetchingStatus(id) {
  return {
    type: actionTypes.AUTOCOMPLETE_FETCHING_DATA,
    id,
  };
}

export function settingFetchingFailed(error, id) {
  return {
    type: actionTypes.AUTOCOMPLETE_FETCHING_FAILED,
    error,
    id,
  };
}

export function setSearchData(data, id) {
  return {
    type: actionTypes.AUTOCOMPLETE_SET_AUTO_SUGGEST_DATA,
    id,
    payload: data,
  };
}

export function updateDatePicker(moveInMonth, moveOutMonth) {
  return {
    type: actionTypes.AUTOCOMPLETE_UPDATE_DATE_PICKER,
    payload: {
      moveInMonth,
      moveOutMonth,
    },
  };
}

export function componentMounted(id) {
  return {
    type: actionTypes.AUTOCOMPLETE_COMPONENT_MOUNTED,
    id,
  };
}



