import generatePath from '~global-js/helpers/router';
import { isRetinaScreen } from '~global-js/helpers/breakpoints';
import { getLocale } from '~global-js/helpers/locale';

export default function formatData(rowData, type = null, highlight = null) {
  const entity = [];

  if (!Array.isArray(rowData) || rowData.length < 1) {
    return null;
  }

  rowData.map((data) => {
    if (type === 'popularCities' && data.city_name) {
      entity.push(formatpopularCitiesData(data));
    } else if (type === 'cities' || data.type === 'city') {
      entity.push(formatCityData(data));
    } else if (type === 'properties' || data.type === 'property') {
      entity.push(formatPropertyData(data));
    } else if (type !== 'areas') {
      entity.push(formatEntityData(data, type || data.type));
    }

    return true;
  });

  if (entity.length > 0 && highlight && highlight.length > 1) {
    entity.map((list) => {
      const newList = list;
      const showText = ['originalName', 'primaryText', 'commonNames'];

      showText.map((key) => {
        if (newList[key]) {
          const str = `(${highlight})`;
          const searchText = new RegExp(str, 'gi');

          if (key === 'primaryText' && newList[key] === highlight) {
            newList[key] = newList[key].replace(
              searchText, '<span class="autocomplete__text__highlight--matches">$&</span>'
            );

            const highlightList = document.querySelector('.autocomplete__text__highlight--matches');

            if (
              highlightList &&
              highlightList.parentElement &&
              highlightList.parentElement.parentElement &&
              highlightList.parentElement.parentElement.parentElement
              ) {
              const highlightListParent = highlightList.parentElement.parentElement.parentElement;
              highlightListParent.classList.add('autocomplete__matches');
            }
          } else {
            newList[key] = newList[key].replace(
              searchText, '<span class="autocomplete__text__highlight">$&</span>'
            );
          }
        }

        return true;
      });

      return true;
    });
  }

  return entity;
}

function formatpopularCitiesData(city) {
  let formattedData = {
    type: 'city',
    primaryText: city.city_name,
    originalName: city.original_name || city.city_slug,
    original: city.original_name || city.city_slug,
    link: generatePath('city_property_listing', {
      countrySlug: city.country_slug,
      citySlug: city.city_slug,
    }),
    attributes: JSON.stringify({
      type: 'city',
      link: generatePath('city_property_listing', {
        countrySlug: city.country_slug,
        citySlug: city.city_slug,
      }),
      name: city.city_name,
      slug: city.city_slug,
      original_name: city.original_name,
      city: {
        name: city.city_name,
        slug: city.city_slug,
      },
      country: {
        name: city.country_name,
        slug: city.country_slug,
      },
    }),
  };

  if (city.country_name) {
    formattedData = Object.assign(formattedData, {
      secondaryText: city.country_name,
    });
  }

  return formattedData;
}

function formatCityData(city) {
  let formattedData = {
    type: 'city',
    primaryText: city.name,
    originalName: city.original_name || city.slug,
    original: city.original_name || city.slug,
    link: generatePath('city_property_listing', {
      countrySlug: city.country.slug,
      citySlug: city.slug,
    }),
    attributes: JSON.stringify({
      type: 'city',
      link: generatePath('city_property_listing', {
        countrySlug: city.country.slug,
        citySlug: city.slug,
      }),
      name: city.name,
      slug: city.slug,
      original_name: city.original_name,
      city: {
        name: city.name,
        slug: city.slug,
      },
      country: {
        name: city.country.name,
        slug: city.country.slug,
        currency_code: city.country.currency_code,
      },
    }),
  };

  if (city.country.name) {
    formattedData = Object.assign(formattedData, {
      secondaryText: `${city.country.name}`,
    });
  }

  if (city.hero_image) {
    formattedData.heroImage = isRetinaScreen() ? city.hero_image_2x : city.hero_image;
  }
  return formattedData;
}

function formatPropertyData(property) {
  let formattedData = {
    type: 'property',
    primaryText: property.name,
    secondaryText: property.city.name,
    originalName: property.original_name || property.name,
    original: property.original_name || property.name,
    link: generatePath('property_detail', {
      countrySlug: property.country.slug,
      citySlug: property.city.slug,
      propertySlug: property.slug,
    }),
    attributes: JSON.stringify({
      type: 'property',
      link: generatePath('property_detail', {
        countrySlug: property.country.slug,
        citySlug: property.city.slug,
        propertySlug: property.slug,
      }),
      ...property,
    }),
  };


  if (property.country.name) {
    formattedData = Object.assign(formattedData, {
      secondaryText: `${property.city.name}, ${property.country.name}`,
    });
  }

  return formattedData;
}


function formatEntityData(entity, type) {
  let pathType = type;

  if (type === 'universities') {
    pathType = 'university';
  }

  if (type === 'popularCities') {
    pathType = entity.type;
  }

  let formattedData = {
    type: pathType,
    primaryText: getLocale() === 'zh-cn' ? entity.name : entity.original_name,
    secondaryText: entity.city.name,
    originalName: entity.original_name || entity.slug,
    original: entity.original_name || entity.slug,
    link: generatePath(`${pathType}_property_listing`, {
      countrySlug: entity.country.slug,
      citySlug: entity.city.slug,
      [`${pathType}Slug`]: entity.slug,
    }),
    attributes: JSON.stringify({
      type: pathType,
      link: generatePath(`${pathType}_property_listing`, {
        countrySlug: entity.country.slug,
        citySlug: entity.city.slug,
        [`${pathType}Slug`]: entity.slug,
      }),
      ...entity,
    }),
  };

  if (entity.country.name) {
    formattedData = Object.assign(formattedData, {
      secondaryText: `${entity.city.name}, ${entity.country.name}`,
    });
  }

  if (entity.common_names && entity.common_names.length > 0) {
    let commonName = '';
    entity.common_names.map((name) => {
      if (commonName === '') {
        commonName = name;
      } else if (commonName.concat(',', name).length <= 20) {
        commonName = commonName.concat(',', name);
      }

      return true;
    });

    formattedData = Object.assign(formattedData, {
      commonNames: commonName,
    });
  }

  return formattedData;
}



