import { getLocale } from '../helpers/locale';
import { getItem, setItem } from '../helpers/storage';

const locale = getLocale();
const LOCAL_STORAGE_KEY_PERSIST_USER_INFO = 'PERSIST_USER_INFO';
const LOCAL_STORAGE_VERSION = 'v3';
const localStorageDefaultFormat = [
  'tenancyLength',
  'tenancyLengthUnit',
  'budget',
  'budgetCurrency',
  'budgetFrequency',
  'destinationUniversity',
  'preferredRoomType',
];

export default function processPersistUserLocalStorageData(citySlug, result, isMobile = false) {
  if (typeof window === 'undefined') {
    return false;
  }

  const mapData = {};
  let localeStorageData = getItem(LOCAL_STORAGE_KEY_PERSIST_USER_INFO);

  // If no localeStorageData init data
  // if data no exist in the localStorageData init data
  // If version not right init data in the localStorage
  if (!localeStorageData || !localeStorageData.data ||
      localeStorageData.version !== LOCAL_STORAGE_VERSION
  ) {
    localeStorageData = {
      data: {
        cities: {},
      },
    };
  }

  if (citySlug) {
    localStorageDefaultFormat.map((item) => {
      if (item !== 'preferredRoomType') {
        mapData[item] = result[item] || null;
      } else {
        mapData[item] = result[item] || [];
      }

      // In mobile version if result is null or [] or undefined will get the value before
      // In desktop version if result is undefined will get the value before
      if (typeof localeStorageData.data.cities[citySlug] !== 'undefined' &&
          typeof localeStorageData.data.cities[citySlug][item] !== 'undefined' &&
          ((isMobile && (result[item] === null ||
          (typeof result[item] === 'object' && result[item].length === 0))) ||
          typeof result[item] === 'undefined')
      ) {
        mapData[item] = localeStorageData.data.cities[citySlug][item];
      }

      return null;
    });

    if (
      localeStorageData.data &&
      localeStorageData.data.cities &&
      localeStorageData.data.cities[citySlug]
    ) {
      delete localeStorageData.data.cities[citySlug];
    }

    localeStorageData.data.cities[citySlug] = mapData;
  }

  if (typeof result.targetMoveInMonth !== 'undefined') {
    localeStorageData.data.targetMoveInMonth = result.targetMoveInMonth || null;
  }

  if (typeof result.targetMoveOutMonth !== 'undefined') {
    localeStorageData.data.targetMoveOutMonth = result.targetMoveOutMonth || null;
  }

  if (typeof result.lastSearch !== 'undefined') {
    localeStorageData.data.lastSearch = result.lastSearch;
  }

  setItem(LOCAL_STORAGE_KEY_PERSIST_USER_INFO, compileLocalStageData(
    localeStorageData.data
  ));

  return true;
}

export function getUserPersistedInfo() {
  return getItem(LOCAL_STORAGE_KEY_PERSIST_USER_INFO);
}

export function getUserPersistedMoveDate() {
  const userData = getItem(LOCAL_STORAGE_KEY_PERSIST_USER_INFO);
  const persistedMoveDate = {
    targetMoveInMonth: null,
    targetMoveOutMonth: null,
  };

  if (userData && userData.data && userData.data.targetMoveInMonth) {
    persistedMoveDate.targetMoveInMonth = userData.data.targetMoveInMonth;
  }

  if (userData && userData.data && userData.data.targetMoveOutMonth) {
    persistedMoveDate.targetMoveOutMonth = userData.data.targetMoveOutMonth;
  }

  return persistedMoveDate;
}

export function getUserLastSearch() {
  const userData = getItem(LOCAL_STORAGE_KEY_PERSIST_USER_INFO);

  if (userData && userData.data && userData.data.lastSearch) {
    return userData.data.lastSearch;
  }

  return null;
}

function compileLocalStageData(localeStorageData) {
  return {
    createTime: new Date().getTime(),
    version: LOCAL_STORAGE_VERSION,
    locale,
    data: localeStorageData,
  };
}



