import { u } from 'umbrellajs/umbrella';
import shortid from 'shortid';
import document from 'global/document';
import BaseComponent from '~global-js/component/base-component';
import template from '~components/date-picker/templates/date-picker.hbs';
import * as actions from '~components/date-picker/js/actions';
import { moveOutTab } from '~components/date-picker/js/reducers';
import handlebars from 'handlebars/runtime';
import generatePath from '~global-js/helpers/router';

export default class DatePickerComponent extends BaseComponent {
  constructor(element, props = {}) {
    if (!props.getState || !props.dispatch || !props.subscribe) {
      throw new Error('You must specify props.getState, props.dispatch and props.subscribe');
    }
    super(element, props);
  }

  init(completeCallback = null) {
    this.renderTemplate();
    this.props.subscribe(this.propagateStoreChanges);

    const monthsAttribute = this.element.getAttribute('data-months');
    this.updateAvailableMonths(monthsAttribute === 'all' ? 'all' : JSON.parse(monthsAttribute));
    // const availableMonths = monthsAttribute === 'all' ? 'all' : JSON.parse(monthsAttribute);
    // this.props.dispatch(actions.initMoveInYears(availableMonths));

    if (this.props.preSelectedMoveInMonth) {
      const moveInMonth = this.props.preSelectedMoveInMonth.slice(-2);
      const moveInMonthString = this.trans(`date_picker.month_label.${moveInMonth}`);
      let moveOutMonthString;

      if (this.props.preSelectedMoveOutMonth) {
        const moveOutMonth = this.props.preSelectedMoveOutMonth.slice(-2);
        moveOutMonthString = this.trans(`date_picker.month_label.${moveOutMonth}`);
      }

      this.props.dispatch(actions.preSelectDates(
        this.props.preSelectedMoveInMonth,
        moveInMonthString,
        this.props.preSelectedMoveOutMonth,
        moveOutMonthString
      ));
    }

    this.render();
    this.addEventListeners();

    if (completeCallback) {
      completeCallback();
    }
  }

  trans(...args) {
    return this.props.i18n.t(...args);
  }

  destroy() {
    this.removeEventListeners();
  }

  updateAvailableMonths(months = 'all') {
    this.props.dispatch(actions.initMoveInYears(months));
  }

  updateMoveMonth(newMoveInMonth, newMoveOutMonth) {
    if (!newMoveInMonth && !newMoveOutMonth) {
      this.props.dispatch(actions.reset());
      return false;
    }

    const moveInMonth = (newMoveInMonth && newMoveInMonth.slice(-2)) || '';
    const moveInMonthString = (moveInMonth && this.trans(`date_picker.month_label.${moveInMonth}`)) || '';
    const moveOutMonth = (newMoveOutMonth && newMoveOutMonth.slice(-2)) || '';
    const moveOutMonthString = (moveOutMonth && this.trans(`date_picker.month_label.${moveOutMonth}`)) || '';

    this.props.dispatch(actions.preSelectDates(
      newMoveInMonth,
      moveInMonthString,
      newMoveOutMonth,
      moveOutMonthString
    ));

    this.render();
    return true;
  }

  renderTemplate() {
    let shortTermUrl = null;

    if (this.props.shortTerm && this.props.shortTerm.citySlug && this.props.shortTerm.countrySlug) {
      shortTermUrl = generatePath('short-term', {
        countrySlug: this.props.shortTerm.countrySlug,
        citySlug: this.props.shortTerm.citySlug,
      });
    }

    handlebars.registerHelper('trans', key => this.trans(key));
    const html = template({
      move_in_date_field_name: this.props.moveInMonthFieldName,
      move_out_date_field_name: this.props.moveOutMonthFieldName,
      id_prefix: shortid.generate(),
      shortTermUrl,
    });

    handlebars.unregisterHelper('trans');

    u(this.element).append(html); // safe append to dom
  }

  render() {
    const state = this.props.getState();
    const moveInMonths = state.years.moveInAvailable[state.years.moveInYear];
    const moveOutMonths = state.years.moveOutAvailable[state.years.moveOutYear];
    const pickerElement = this.getElement('picker');

    if (this.props.includeBorder) {
      pickerElement.classList.remove(this.props.noBorderModifier);
    } else {
      pickerElement.classList.add(this.props.noBorderModifier);
    }

    // move in/out months
    DatePickerComponent.renderMonths(
      moveInMonths,
      state.months.moveInMonth,
      this.getElementList('moveInMonthInput')
    );
    DatePickerComponent.renderMonths(
      moveOutMonths,
      state.months.moveOutMonth,
      this.getElementList('moveOutMonthInput')
    );

    // tab selection
    this.moveInTabInput.checked = state.months.tab === 'move-in';
    this.moveOutTabInput.checked = state.months.tab === 'move-out';

    // month empty state
    this.getElement('moveInMonthEmptyInput').checked = !state.months.moveInMonth;
    this.getElement('moveOutMonthEmptyInput').checked = !state.months.moveOutMonth;

    // year selection
    this.getElement('moveInYear').textContent = state.years.moveInYear;
    this.getElement('moveOutYear').textContent = state.years.moveOutYear;
    this.renderYearNavigator(
      this.getElement('moveInYearIncrement'),
      state.years.moveInYearIncrementEnabled
    );
    this.renderYearNavigator(
      this.getElement('moveInYearDecrement'),
      state.years.moveInYearDecrementEnabled
    );
    this.renderYearNavigator(
      this.getElement('moveOutYearIncrement'),
      state.years.moveOutYearIncrementEnabled
    );
    this.renderYearNavigator(
      this.getElement('moveOutYearDecrement'),
      state.years.moveOutYearDecrementEnabled
    );

    // formatted dates on tabs
    if (state.months.moveInMonthFormatted) {
      this.moveInTabLabel.textContent = state.months.moveInMonthFormatted;
      this.moveInTabLabel.parentNode.classList.add('date-picker__tab--has-value');
    } else {
      this.moveInTabLabel.textContent = this.moveInTabLabel.getAttribute('data-tab-name');
      this.moveInTabLabel.parentNode.classList.remove('date-picker__tab--has-value');
    }
    if (state.months.moveOutMonthFormatted) {
      this.moveOutTabLabel.textContent = state.months.moveOutMonthFormatted;
      this.moveOutTabLabel.parentNode.classList.add('date-picker__tab--has-value');
    } else {
      this.moveOutTabLabel.textContent = this.moveOutTabLabel.getAttribute('data-tab-name');
      this.moveOutTabLabel.parentNode.classList.remove('date-picker__tab--has-value');
    }
  }

  renderYearNavigator(navigatorElement, enabled) {
    if (enabled) {
      navigatorElement.classList.remove(this.props.yearNavigatorElementHiddenModifier);
    } else {
      navigatorElement.classList.add(this.props.yearNavigatorElementHiddenModifier);
    }
  }

  static renderMonths(monthsState, currentMonth, monthInputs) {
    if (monthsState !== undefined && Object.keys(monthsState).length) {
      for (let i = 0; i < monthsState.length; i++) {
        const input = monthInputs[i];
        input.value = monthsState[i].value;
        input.disabled = !monthsState[i].enabled;
        input.checked = input.value === currentMonth;
      }
    }
  }

  addEventListeners() {
    if (this.getElement('moveInMonthsFieldset')) {
      this.getElement('moveInMonthsFieldset')
        .addEventListener('change', this.onMoveInMonthSelected);
    }
    if (this.getElement('moveOutMonthsFieldset')) {
      this.getElement('moveOutMonthsFieldset')
        .addEventListener('change', this.onMoveOutMonthSelected);
    }
    if (this.getElement('moveInYearIncrement')) {
      this.getElement('moveInYearIncrement')
        .addEventListener('click', this.onMoveInYearIncrementClick);
    }
    if (this.getElement('moveInYearDecrement')) {
      this.getElement('moveInYearDecrement')
        .addEventListener('click', this.onMoveInYearDecrementClick);
    }
    if (this.getElement('moveOutYearIncrement')) {
      this.getElement('moveOutYearIncrement')
        .addEventListener('click', this.onMoveOutYearIncrementClick);
    }
    if (this.getElement('moveOutYearDecrement')) {
      this.getElement('moveOutYearDecrement')
        .addEventListener('click', this.onMoveOutYearDecrementClick);
    }
    if (this.getElementList('resetLink')) {
      this.getElementList('resetLink')
        .forEach(link => link.addEventListener('click', this.onResetClick));
    }

    this.element.addEventListener('click', this.onPickerClicked);

    if (document.querySelector('.date-picker__wrapper')) {
      document.querySelector('.date-picker__wrapper').addEventListener('click', this.onDocumentClick);
    }

    if (this.props.shortTerm) {
      this.getElement('shortTermLink')
        .addEventListener('click', this.onShortTermClick);
    }
  }

  removeEventListeners() {
    if (this.getElement('moveInMonthsFieldset')) {
      this.getElement('moveInMonthsFieldset')
        .removeEventListener('change', this.onMoveInMonthSelected);
    }
    if (this.getElement('moveOutMonthsFieldset')) {
      this.getElement('moveOutMonthsFieldset')
        .removeEventListener('change', this.onMoveOutMonthSelected);
    }
    if (this.getElement('moveInYearIncrement')) {
      this.getElement('moveInYearIncrement')
        .removeEventListener('click', this.onMoveInYearIncrementClick);
    }
    if (this.getElement('moveInYearDecrement')) {
      this.getElement('moveInYearDecrement')
        .removeEventListener('click', this.onMoveInYearDecrementClick);
    }
    if (this.getElement('moveOutYearIncrement')) {
      this.getElement('moveOutYearIncrement')
        .removeEventListener('click', this.onMoveOutYearIncrementClick);
    }
    if (this.getElement('moveOutYearDecrement')) {
      this.getElement('moveOutYearDecrement')
        .removeEventListener('click', this.onMoveOutYearDecrementClick);
    }
    if (this.getElementList('resetLink')) {
      this.getElementList('resetLink')
        .forEach(link => link.removeEventListener('click', this.onResetClick));
    }

    this.element.removeEventListener('click', this.onPickerClicked);

    if (document.querySelector('.date-picker__wrapper')) {
      document.querySelector('.date-picker__wrapper').removeEventListener('click', this.onDocumentClick);
    }

    if (this.props.shortTerm) {
      this.getElement('shortTermLink')
        .removeEventListener('click', this.onShortTermClick);
    }
  }

  propagateStoreChanges = () => {
    this.render();
  };

  onShortTermClick = () => {
    if (this.props.onShortTermClick) {
      this.props.onShortTermClick();
    }
  };

  onPickerClicked = (e) => {
    const target = e.target;
    if (target.classList && target.classList.contains('js-date-picker-tab')) {
      this.props.dispatch(actions.selectTab(target.value));
      document.addEventListener('click', this.onDocumentClick, true);
      document.addEventListener('touchstart', this.onDocumentClick, true);
      if (typeof this.props.onTabClick === 'function') {
        this.props.onTabClick(target.value);
      }
    }
  };

  onMoveInMonthSelected = (e) => {
    const monthName = e.target.getAttribute('data-month-name');
    const monthValue = e.target.value;

    this.props.dispatch(actions.initMoveOutYears(monthValue));
    this.props.dispatch(actions.selectMoveInMonth(monthValue, monthName));
    this.props.dispatch(actions.selectTab(moveOutTab));
    this.selectedMoveIn();
  };

  onMoveOutMonthSelected = (e) => {
    const monthName = e.target.getAttribute('data-month-name');
    const monthValue = e.target.value;

    this.props.dispatch(actions.selectMoveOutMonth(monthValue, monthName));
    this.hidePicker(true);
  };

  onMoveInYearIncrementClick = () => {
    this.props.dispatch(actions.incrementMoveInYear());
  };

  onMoveInYearDecrementClick = () => {
    this.props.dispatch(actions.decrementMoveInYear());
  };

  onMoveOutYearIncrementClick = () => {
    this.props.dispatch(actions.incrementMoveOutYear());
  };

  onMoveOutYearDecrementClick = () => {
    this.props.dispatch(actions.decrementMoveOutYear());
  };

  onResetClick = () => {
    this.props.dispatch(actions.reset());
    this.hidePicker();
  };

  onDocumentClick = (e) => {
    if (!this.element.contains(e.target) || !u(e.target).closest('.date-picker')) {
      const state = this.props.getState();
      e.stopPropagation();
      if (state.months.tab) {
        setTimeout(() => {
          this.hidePicker();
        }, 300);
      }
    }
  };

  selectedMoveIn() {
    if (this.props.moveInSelected) {
      const state = this.props.getState();
      this.props.dispatch(this.props.moveInSelected(
          state.months.moveInMonth,
          state.months.moveOutMonth
      ));
    }
  }

  onkeyDown = (e) => {
    if (e.keyCode === 27) {
      this.onCloseShortTermModal();
    }
  };

  onCloseShortTermModal = () => {
    const modal = document.querySelector(this.props.elementSelectors.shortTermModal);

    if (modal.remove) {
      modal.remove();
    } else {
      modal.removeNode(true);
    }

    document.removeEventListener('keydown', this.onkeyDown);
    document.body.classList.remove('no-scroll');
  };

  hidePicker(fieldsSelected = false) {
    const state = this.props.getState();
    document.removeEventListener('click', this.onDocumentClick, true);
    document.removeEventListener('touchstart', this.onDocumentClick, true);
    this.props.dispatch(actions.selectTab(null));
    this.props.dispatch(this.props.hidePickerAction(
      state.months.moveInMonth,
      state.months.moveOutMonth,
      fieldsSelected,
    ));
  }

  static defaultProps = {
    elementSelectors: {
      moveInMonthsFieldset: '.js-date-picker-move-in-fieldset',
      moveOutMonthsFieldset: '.js-date-picker-move-out-fieldset',
      tabInput: '.js-date-picker-tab',
      moveInMonthInput: '.js-date-picker-move-in-month',
      moveOutMonthInput: '.js-date-picker-move-out-month',
      moveInMonthEmptyInput: '.js-date-picker-move-in-month-empty',
      moveOutMonthEmptyInput: '.js-date-picker-move-out-month-empty',
      moveInYear: '.js-date-picker-move-in-year',
      moveOutYear: '.js-date-picker-move-out-year',
      moveInYearIncrement: '.js-date-picker-move-in-year-increment',
      moveInYearDecrement: '.js-date-picker-move-in-year-decrement',
      moveOutYearIncrement: '.js-date-picker-move-out-year-increment',
      moveOutYearDecrement: '.js-date-picker-move-out-year-decrement',
      resetLink: '.js-date-picker-reset',
      tabLabel: '.js-date-picker-tab-label',
      picker: '.date-picker',
      shortTermLink: '.js-date-picker-short-term',
    },
    moveInMonthFieldName: 'move-in',
    moveOutMonthFieldName: 'move-out',
    yearNavigatorElementHiddenModifier: 'date-picker__arrow--hidden',
    includeBorder: true,
    noBorderModifier: 'date-picker--no-border',
    preSelectedMoveInMonth: null,
    preSelectedMoveOutMonth: null,
    onTabClick: null,
    shortTerm: null,
    onShortTermClick: null,
  }

  get moveInTabInput() {
    return this.getElementList('tabInput')[0];
  }

  get moveOutTabInput() {
    return this.getElementList('tabInput')[1];
  }

  get moveInTabLabel() {
    return this.getElementList('tabLabel')[0];
  }

  get moveOutTabLabel() {
    return this.getElementList('tabLabel')[1];
  }
}



