import { u } from 'umbrellajs/umbrella';
import document from 'global/document';
import * as actions from '~components/autocomplete/js/actions';
import BaseComponent from '~global-js/component/base-component';
import popularCitiesTemplate from '~components/autocomplete/templates/popular-cities.hbs';
import listTemplate from '~components/autocomplete/templates/list.hbs';
import formatData from '~components/autocomplete/js/helpers';
import AutoCompleteInteraction from '~components/autocomplete/js/interaction';
import { event } from '~global-js/helpers/tracking';
import { isLargeScreen } from '~global-js/helpers/breakpoints';
import { getUserPersistedMoveDate } from '~global-js/helpers/persist-user-info';
import DatePickerComponent from '~components/date-picker/js/component';
import { defaultState as defaultAutoSuggestState } from '~components/autocomplete/js/reducers/autoSuggest';
import { defaultState as defaultPreSuggestState } from '~components/autocomplete/js/reducers/preSuggest';
import { getLocale } from '~global-js/helpers/locale';

export default class AutoCompleteComponent extends BaseComponent {
  constructor(element, id, props = {}) {
    props.chooseBehavior = props.enableDatePicker ? 'fill' : props.chooseBehavior; // eslint-disable-line no-param-reassign

    super(element, props);
    props.store.dispatch(actions.registerComponent(id));

    this.form = u(element).closest('form');
    this.suggestionBox = u('<div>').addClass('autocomplete');
    this.clearButton = u('<span>').addClass('autocomplete__clear');
    this.datePickerElement = u('<div>').addClass('autocomplete__datepicker').attr('data-months', 'all');
    this.datePicker = null;
    this.interactionComponent = null;
    this.unsubscribe = null;
    this.timeout = {
      inputTimeout: null,
      hideSuggestionTimeout: null,
    };

    this.state = {
      autoSuggest: defaultAutoSuggestState,
      preSuggest: defaultPreSuggestState,
      id,
      searchTerm: this.element.value || null,
      inputValue: null,
      isClearSearch: false,
    };
  }

  init() {
    // Init suggestion box
    if (this.props.customSuggestionBoxId) {
      this.suggestionBox = u(document.getElementById(this.props.customSuggestionBoxId));
    } else {
      u(this.element).after(this.suggestionBox);
    }

    if (this.props.suggestionBoxClass) {
      this.suggestionBox.addClass(this.props.suggestionBoxClass);
    }

    if (this.props.clearButtonStyle) {
      Object.keys(this.props.clearButtonStyle).forEach((key) => {
        this.clearButton.first().style[key] = this.props.clearButtonStyle[key];
      });
    }

    this.form.addClass('autocomplete__form');
    u(this.element).addClass('autocomplete__input');

    u(this.element).after(this.clearButton);
    this.addEventListener();
    if (this.state.searchTerm) {
      this.props.store.dispatch(
        actions.fetchSeachData(this.state.searchTerm, this.props, this.state.id, false)
      );
    } else {
      const state = this.props.store.getState();
      this.state = Object.assign({}, this.state, {
        autoSuggest: state.autoSuggest[this.state.id],
        preSuggest: state.preSuggest,
      });

      this.render();
    }
    this.unsubscribe = this.props.store.subscribe(this.propagateStoreChanges);
    // Init Datepicker container
    if (this.props.enableDatePicker) {
      u(this.element).parent().after(this.datePickerElement);
      const persistedMoveDate = getUserPersistedMoveDate();

      this.datePicker = new DatePickerComponent(this.datePickerElement.first(), {
        getState: () => this.props.store.getState().datePicker,
        dispatch: this.props.store.dispatch,
        subscribe: this.props.store.subscribe,
        i18n: this.props.i18n,
        preSelectedMoveInMonth: persistedMoveDate.targetMoveInMonth,
        preSelectedMoveOutMonth: persistedMoveDate.targetMoveOutMonth,
        hidePickerAction: this.onHideDatePicker,
        includeBorder: false,
        searchPlacement: this.props.searchPlacement,
        onTabClick: (tab) => {
          this.form.addClass('autocomplete__form--date-picker-active');
          if (!this.interactionComponent.isDatePickerAutoOpen()) {
            event({
              c: this.props.searchPlacement,
              a: 'click',
              l: `datePicker:open:${tab}`,
              mp: [{
                type: 'track',
                key: 'Clicked Search Date Picker',
                params: {
                  'Search Placement': this.props.searchPlacement,
                  'Date Click Type': `Open ${tab}`,
                },
              }],
            });
          }
        },
      });
      this.datePicker.init(() => {
        this.props.store.dispatch(actions.componentMounted(this.state.id));
      });

      this.props.store.dispatch(
        actions.updateDatePicker(
          persistedMoveDate.targetMoveInMonth,
          persistedMoveDate.targetMoveOutMonth
        )
      );
    } else {
      this.props.store.dispatch(actions.componentMounted(this.state.id));
    }
  }

  addEventListener = () => {
    this.element.addEventListener('focus', this.handleInputElementFocus);
    this.element.addEventListener('input', this.handleInputElementInputing);
    this.element.addEventListener('blur', this.handleInputElementBlur);
    this.registerInteractionComponent();
    this.clearButton.on('click', this.clearSearch);

    if (this.element === document.activeElement) {
      setTimeout(() => {
        this.handleInputElementFocus();
      }, 300);
    }
  };

  getRecommendationType = (value) => {
    let recommendationType = 'Recommend';
    if (this.state.preSuggest.recentSearch && this.state.preSuggest.recentSearch.length > 0) {
      this.state.preSuggest.recentSearch.map((list) => {
        if (list.primaryText === value || list.originalName === value) {
          recommendationType = 'History';
        }

        return true;
      });
    }

    return recommendationType;
  };

  handleInputElementFocus = (e) => {
    let inputValue = null;
    if (e) {
      inputValue = e.target.value;
    } else {
      inputValue = this.element.value;
    }

    this.setInputValue(inputValue);
    clearTimeout(this.timeout.hideSuggestionTimeout);
    this.suggestionBox.addClass('autocomplete--active');
    this.form.addClass('autocomplete__form--input-active');

    if (this.state.searchTerm) {
      this.clearButton.addClass('autocomplete__clear--active');
      if (isLargeScreen()) {
        setTimeout(() => {
          this.element.setSelectionRange(0, this.element.value.length);
        }, 0); // Fix Safari reverse process, selectionRange -> focus
      }
    }

    if (!this.state.isClearSearch) {
      event({
        c: this.props.searchPlacement,
        a: 'click',
        l: 'search > searchBox:focus',
        mp: [{
          type: 'track',
          key: 'Focused Search Input',
          params: {
            'Search Placement': this.props.searchPlacement,
            'Input Status': inputValue ? 'Pre-filled' : 'Empty',
          },
        }],
      });
    }

    if (!this.state.searchTerm) {
      event({
        c: this.props.searchPlacement,
        a: 'view',
        l: `search > defaultView:open{${
          this.state.preSuggest.recentSearch && this.state.preSuggest.recentSearch.length > 0 ?
          'recentSearches' : 'popularDestinations'
        }}`,
        mp: [{
          type: 'track',
          key: 'Viewed Search Recommendation',
          params: {
            'Search Placement': this.props.searchPlacement,
            'Recommendation Type': this.getRecommendationType(inputValue),
            'AB Testing Variant': 'Old Version',
          },
        }],
      });
    }

    this.state.isClearSearch = false;
  };

  handleInputElementBlur = () => {
    this.form.removeClass('autocomplete__form--input-active');
    this.timeout.hideSuggestionTimeout = setTimeout(() => {
      if (!this.suggestionBox.hasClass('autocomplete--static') || (this.state.searchTerm !== '' && this.state.searchTerm !== null)) {
        this.suggestionBox.removeClass('autocomplete--active');
      }

      this.clearButton.removeClass('autocomplete__clear--active');
    }, 300);
  };

  handleInputElementInputing = (e) => {
    if (e.target.value) {
      this.clearButton.addClass('autocomplete__clear--active');
    } else {
      this.clearButton.removeClass('autocomplete__clear--active');
    }

    if (this.state.searchTerm === e.target.value) {
      return;
    }

    this.setInputValue(e.target.value);

    clearTimeout(this.timeout.inputTimeout);
    this.state.searchTerm = e.target.value;
    this.element.setAttribute('data-value', '');
    this.element.setAttribute('data-detail', '');

    this.timeout.inputTimeout = setTimeout(() => {
      this.props.store.dispatch(
        actions.fetchSeachData(this.state.searchTerm, this.props, this.state.id)
      );
    }, 500);
  };

  setInputValue = (value) => {
    if (value) {
      this.state = Object.assign({}, this.state, {
        inputValue: value,
      });

      let template = '';
      template += this.renderSearchResults();
      this.suggestionBox.html(template);
      this.updateInteractionComponent(true);
    }

    const highlightList = document.querySelector('.autocomplete__text__highlight--matches');

    if (
      highlightList &&
      highlightList.parentElement &&
      highlightList.parentElement.parentElement &&
      highlightList.parentElement.parentElement.parentElement
      ) {
      const highlightListParent = highlightList.parentElement.parentElement.parentElement;
      highlightListParent.classList.add('autocomplete__matches');
    }
  }

  clearSearch = () => {
    this.state.searchTerm = '';
    this.element.value = '';
    this.element.removeAttribute('data-value');
    this.element.removeAttribute('data-detail');
    this.props.store.dispatch(
      actions.fetchSeachData(this.state.searchTerm, this.props, this.state.id)
    );
    this.state.isClearSearch = true;
    this.element.focus();
    this.clearButton.removeClass('autocomplete__clear--active');
  };

  propagateStoreChanges = () => {
    let autoSuggestUpdated = false;
    let preSuggestUpdated = false;
    const newState = this.props.store.getState();

    if (JSON.stringify(this.state.autoSuggest.payload)
        !== JSON.stringify(newState.autoSuggest[this.state.id].payload)) {
      this.state.autoSuggest = newState.autoSuggest[this.state.id];
      autoSuggestUpdated = true;
    }

    if (JSON.stringify(this.state.preSuggest) !== JSON.stringify(newState.preSuggest)) {
      this.state.preSuggest = newState.preSuggest;
      preSuggestUpdated = true;
    }

    if (autoSuggestUpdated || preSuggestUpdated) {
      this.render();
    }
  };

  onHideDatePicker = (moveInMonth, moveOutMonth, allFieldsSelected) => (dispatch, getState) => {
    const previousState = getState().preSuggest.datePicker;
    let moveInMonthChanged = moveInMonth !== previousState.moveInMonth;
    let moveOutMonthChanged = moveOutMonth !== previousState.moveOutMonth;

    if (!moveInMonth && previousState.moveInMonth === null) {
      moveInMonthChanged = false;
    }

    if (!moveOutMonth && previousState.moveOutMonth === null) {
      moveOutMonthChanged = false;
    }

    this.form.removeClass('autocomplete__form--date-picker-active');

    if (moveInMonthChanged || moveOutMonthChanged) {
      dispatch(
        actions.updateDatePicker(moveInMonth, moveOutMonth)
      );

      if (moveInMonthChanged) {
        const mpParams = {
          'Search Placement': this.props.searchPlacement,
          'Date Click Type': 'Reset',
        };
        let gaLabel = 'search > datePicker:resetBtn';

        if (moveInMonth) {
          mpParams['Date Click Type'] = 'Move In Month';
          mpParams['Move Date Value'] = moveInMonth;
          gaLabel = `search > datePicker:selectMoveIn:${moveInMonth}`;
        }

        if (!moveOutMonthChanged) {
          mpParams['Date Click Type'] = 'Not Certain';
        }

        if (!moveInMonth && !moveOutMonth) {
          mpParams['Date Click Type'] = 'Reset';
        }

        event({
          c: this.props.searchPlacement,
          a: 'click',
          l: gaLabel,
          mp: [{
            type: 'track',
            key: 'Clicked Search Date Picker',
            params: mpParams,
          }],
        });
      }

      if (moveOutMonthChanged && allFieldsSelected) {
        const mpParams = {
          'Search Placement': this.props.searchPlacement,
          'Date Click Type': 'Not Certain',
        };
        let gaLabel = 'search > datePicker:notCertain';

        if (moveOutMonth) {
          mpParams['Date Click Type'] = 'Move Out Month';
          mpParams['Move Date Value'] = moveOutMonth;
          gaLabel = `search > datePicker:selectMoveOut:${moveOutMonth}`;
        }
        event({
          c: this.props.searchPlacement,
          a: 'click',
          l: gaLabel,
          mp: [{
            type: 'track',
            key: 'Clicked Search Date Picker',
            params: mpParams,
          }],
        });
      }

      if (allFieldsSelected && this.element.getAttribute('data-detail')) {
        this.interactionComponent.handleRedirectWithDatePicker({
          clickType: 'Move out',
        });
      }
    } else if (allFieldsSelected) {
      event({
        c: this.props.searchPlacement,
        a: 'click',
        l: 'search > datePicker:notCertain',
        mp: [{
          type: 'track',
          key: 'Clicked Search Date Picker',
          params: {
            'Search Placement': this.props.searchPlacement,
            'Date Click Type': 'Not Certain',
          },
        }],
      });
    }
  };

  render = () => {
    let template = '';
    if (this.state.searchTerm && this.state.searchTerm.length !== 0) {
      template += this.renderSearchResults();
    } else {
      if (this.props.enableRecentSearch && this.state.preSuggest.recentSearch.length > 0) {
        template += this.renderResults({
          baseClass: 'autocomplete--popular',
          title: this.props.i18n.t('global.search.label.recent_search'),
          list: this.state.preSuggest.recentSearch,
          clickType: 'Recent search',
          locale: getLocale() === 'zh-cn',
          platform: isLargeScreen() ? 'desktop' : 'mobile',
          simpleList: this.props.simpleList,
        });
      } else if (this.props.enablePopularCities) {
        template += this.renderPopularCities();
      }

      if (
        this.suggestionBox.hasClass('autocomplete--active')
        && document.activeElement === this.element
      ) {
        event({
          c: this.props.searchPlacement,
          a: 'view',
          l: `search > defaultView:open{${
            this.state.preSuggest.recentSearch && this.state.preSuggest.recentSearch.length > 0 ?
            'recentSearches' : 'popularDestinations'
          }}`,
          mp: [{
            type: 'track',
            key: 'Viewed Search Recommendation',
            params: {
              'Search Placement': this.props.searchPlacement,
              'Recommendation Type': this.getRecommendationType(this.state.inputValue),
              'AB Testing Variant': 'Old Version',
            },
          }],
        });
      }
    }

    this.suggestionBox.html(template);
    this.bindTrackingEvents();
    this.updateInteractionComponent();
    return true;
  };

  renderResults = ({ ...props }) => listTemplate({ ...props });

  renderPopularCities = () =>
    popularCitiesTemplate({
      title: this.props.i18n.t('global.search.label.popular_cities'),
      list: this.state.preSuggest.popularCities,
      clickType: 'Popular city',
    });

  renderSearchResults = () => {
    let formattedList = [];
    let template = '';
    const results = this.state.autoSuggest.payload;

    if (results !== null) {
      const sortResult = [results];
      sortResult.forEach((item) => {
        Object.keys(item).map((key) => {
          const data = formatData(item[key], key);
          if (data && data.length > 0) {
            formattedList = formattedList.concat(
              formatData(item[key], key, this.state.inputValue)
            );
          }
          return true;
        });
      });
    }

    if (formattedList.length > 0) {
      template = this.renderResults({
        baseClass: 'autocomplete--search-results',
        list: formattedList,
        clickType: 'Auto',
        locale: getLocale() === 'zh-cn',
        platform: isLargeScreen() ? 'desktop' : 'mobile',
        simpleList: this.props.simpleList,
      });
    } else if (this.state.autoSuggest.status !== 'FETCHING' && this.props.showNoResultHint) {
      template = this.renderResults({
        noResult: true,
        locale: getLocale() === 'zh-cn',
        platform: isLargeScreen() ? 'desktop' : 'mobile',
        noResultText: this.props.i18n.t('global.search.label.no_results')
          + this.props.i18n.t('global.search.no_result.label.try_again'),
        simpleList: this.props.simpleList,
      });
    }

    return template;
  };

  registerInteractionComponent = () => {
    if (this.interactionComponent) {
      this.destroyInteractionComponent();
    }

    this.interactionComponent = new AutoCompleteInteraction(
      this.element,
      this.form.first(),
      this.suggestionBox,
      this.props
    );

    this.interactionComponent.init();
  };

  updateInteractionComponent = (noPerFill) => {
    if (this.interactionComponent) {
      this.interactionComponent.update(this.element, this.suggestionBox, noPerFill);
    }
  };

  destroyInteractionComponent = () => {
    if (this.interactionComponent) {
      this.interactionComponent.destroy();
      this.interactionComponent = null;
    }
  };

  bindTrackingEvents = () => {
    if (this.props.chooseBehavior === 'fill') {
      return;
    }

    const trackingElements = this.suggestionBox.find('[data-event]');

    trackingElements.nodes.map((element) => {
      const data = JSON.parse(element.getAttribute('data-event'));
      const li = u(element).closest('li').first();
      const detail = JSON.parse(li.getAttribute('data-detail'));
      let finalTouch = 'Auto Suggestion';
      const moveInMonth = this.state.preSuggest.datePicker.moveInMonth;
      const moveOutMonth = this.state.preSuggest.datePicker.moveOutMonth;
      let dateStatus = 'Without Both';

      if (moveOutMonth) {
        dateStatus = 'With Both';
      } else if (moveInMonth) {
        dateStatus = 'With Move In';
      }

      if (data.clickType === 'Popular city') {
        finalTouch = 'Recommend';
      }

      if (data.clickType === 'Recent search') {
        finalTouch = 'History';
      }

      // Not enabled datepicker
      element.addEventListener('click', () => {
        event({
          mp: [
            {
              type: 'track',
              key: 'Searched',
              params: {
                'Search Method': finalTouch,
                'Search Page Type': detail.type === 'Area' ? 'null' : detail.type,
                'Search Slug': detail.slug,
                'Search Placement': this.props.searchPlacement,
                'Date Status': dateStatus,
                'Final Touch': finalTouch,
                'AB Testing Variant': 'Old Version',
              },
            },
          ],
        });

        if (data.clickType === 'Auto' || data.clickType === 'Execute') {
          event({
            c: this.props.searchPlacement,
            a: 'click',
            l: `search > searchExecution:${data.clickType}`,
            emarsys: { 'Search Query': detail.slug },
          });
        } else {
          event({
            c: this.props.searchPlacement,
            a: 'click',
            l: `search > ${data.clickType}:${detail.slug}`,
            emarsys: { 'Search Query': detail.slug },
          });
        }
      });

      return true;
    });
  };

  destroy() {
    if (this.props.customSuggestionBoxId) {
      this.suggestionBox.html('');
    } else if (this.suggestionBox) {
      this.suggestionBox.remove();
    }

    if (this.props.enableDatePicker) {
      if (this.datePicker) {
        this.datePicker.destroy();
      }

      this.datePickerElement.remove();
    }

    if (this.clearButton) {
      this.clearButton.remove();
    }

    if (this.unsubscribe) {
      this.unsubscribe();
    }
    this.destroyInteractionComponent();
    this.element.removeEventListener('focus', this.handleInputElementFocus);
    this.element.removeEventListener('input', this.handleInputElementInputing);
    this.element.removeEventListener('blur', this.handleInputElementBlur);

    this.props.store.dispatch(actions.destroyComponent(this.state.id));
  }

  static defaultProps = {
    searchType: ['city', 'university', 'property'],
    suggestionBoxClass: null,
    customSuggestionBoxId: null,
    enableRecentSearch: false,
    enablePopularCities: false,
    enableDatePicker: false,
    enableModal: true,
    showNoResultHint: true,
    chooseBehavior: 'redirect',
    searchPlacement: null,
    lastSearch: null,
    defaultValue: null,
    simpleList: false,
  }
}



