import Enum from '~global-js/helpers/enum';

export const actionTypes = new Enum(
  'DATE_PICKER_INIT_MOVE_IN_YEARS',
  'DATE_PICKER_INIT_MOVE_OUT_YEARS',
  'DATE_PICKER_SELECT_MOVE_IN_MONTH',
  'DATE_PICKER_SELECT_MOVE_OUT_MONTH',
  'DATE_PICKER_SELECT_TAB',
  'DATE_PICKER_RESET_MONTHS',
  'DATE_PICKER_RESET_YEARS',
  'DATE_PICKER_INCREMENT_MOVE_IN_YEAR',
  'DATE_PICKER_DECREMENT_MOVE_IN_YEAR',
  'DATE_PICKER_INCREMENT_MOVE_OUT_YEAR',
  'DATE_PICKER_DECREMENT_MOVE_OUT_YEAR',
  'DATE_PICKER_SELECT_MOVE_IN_YEAR',
  'DATE_PICKER_SELECT_MOVE_OUT_YEAR',
  'DATE_PICKER_SET_IS_PRE_SELECTING'
);

export function initMoveInYears(monthMapping) {
  return {
    type: actionTypes.DATE_PICKER_INIT_MOVE_IN_YEARS,
    payload: {
      monthMapping,
    },
  };
}

export function initMoveOutYears(moveInMonth) {
  return {
    type: actionTypes.DATE_PICKER_INIT_MOVE_OUT_YEARS,
    payload: {
      moveInMonth,
    },
  };
}

export function selectMoveInMonth(month, monthString) {
  return {
    type: actionTypes.DATE_PICKER_SELECT_MOVE_IN_MONTH,
    payload: {
      month,
      monthString,
    },
  };
}

export function selectMoveOutMonth(month, monthString) {
  return {
    type: actionTypes.DATE_PICKER_SELECT_MOVE_OUT_MONTH,
    payload: {
      month,
      monthString,
    },
  };
}

export function selectTab(tab = null) {
  return {
    type: actionTypes.DATE_PICKER_SELECT_TAB,
    payload: {
      tab,
    },
  };
}

export function resetMonths() {
  return {
    type: actionTypes.DATE_PICKER_RESET_MONTHS,
    payload: {},
  };
}

export function resetYears() {
  return {
    type: actionTypes.DATE_PICKER_RESET_YEARS,
    payload: {},
  };
}

export function reset() {
  return (dispatch) => {
    dispatch(resetMonths());
    dispatch(resetYears());
    dispatch(selectTab());
  };
}

export function incrementMoveInYear() {
  return {
    type: actionTypes.DATE_PICKER_INCREMENT_MOVE_IN_YEAR,
    payload: {},
  };
}

export function decrementMoveInYear() {
  return {
    type: actionTypes.DATE_PICKER_DECREMENT_MOVE_IN_YEAR,
    payload: {},
  };
}

export function incrementMoveOutYear() {
  return {
    type: actionTypes.DATE_PICKER_INCREMENT_MOVE_OUT_YEAR,
    payload: {},
  };
}

export function decrementMoveOutYear() {
  return {
    type: actionTypes.DATE_PICKER_DECREMENT_MOVE_OUT_YEAR,
    payload: {},
  };
}

export function selectMoveInYear(year) {
  return {
    type: actionTypes.DATE_PICKER_SELECT_MOVE_IN_YEAR,
    payload: { year },
  };
}

export function selectMoveOutYear(year) {
  return {
    type: actionTypes.DATE_PICKER_SELECT_MOVE_OUT_YEAR,
    payload: { year },
  };
}

export function setIsPreSelecting(isPreSelecting) {
  return {
    type: actionTypes.DATE_PICKER_SET_IS_PRE_SELECTING,
    payload: { isPreSelecting },
  };
}

export function preSelectDates(
  moveInMonth,
  moveInMonthString,
  moveOutMonth,
  moveOutMonthString
) {
  return (dispatch) => {
    const isMoveOutSelected = moveOutMonth && moveOutMonthString;

    const moveInYear = new Date(moveInMonth).getFullYear();
    dispatch(selectMoveInYear(moveInYear));
    if (isMoveOutSelected) {
      dispatch(setIsPreSelecting(true));
    }
    dispatch(initMoveOutYears(moveInMonth));
    dispatch(selectMoveInMonth(moveInMonth, moveInMonthString));

    if (isMoveOutSelected) {
      const moveOutYear = new Date(moveOutMonth).getFullYear();
      dispatch(selectMoveOutYear(moveOutYear));
      dispatch(selectMoveOutMonth(moveOutMonth, moveOutMonthString));
      dispatch(setIsPreSelecting(false));
    }
  };
}



