import 'classlist-polyfill';
import 'es6-promise/auto';
import { u } from 'umbrellajs/umbrella';
import { Promise } from 'rsvp';
import AutoCompleteComponent from '~components/autocomplete/js/component';
import AutoCompleteModalComponent from '~components/autocomplete/js/modal';
import AutoCompleteModalWithDatePickerComponent from '~components/autocomplete/js/modal-with-datepicker';
import document from 'global/document';
import store from '~components/autocomplete/js/store';
import * as actions from '~components/autocomplete/js/actions';
import initI18n from '~components/i18n/i18next';
import { resetStatus, buildStatus } from '~components/autocomplete/js/constants';
import { isSmallScreen } from '~global-js/helpers/breakpoints';
import { getUserLastSearch } from '~global-js/helpers/persist-user-info';
import { clearCache } from '~global-js/helpers/locale';
import modalTemplate from '~components/autocomplete/templates/modal.hbs';
import modalWithDatePickerTemplate from '~components/autocomplete/templates/modal-with-datepicker.hbs';
import './css/autocomplete.scss';

const reset = {
  flag: resetStatus.IDLE,
  queue: false,
};

let components = [];
let i18next = null;

function onDocumentReady() {
  initI18n().then(() => {
    i18next = window.hurricane.i18next;

    if (isSmallScreen()) {
      const template = modalTemplate({
        cancel: i18next.t('global.search.form_input.cancel_button'),
      });
      u(document.body).append(template);

      const templateWithDatePicker = modalWithDatePickerTemplate({
        cancel: i18next.t('global.search.form_input.cancel_button'),
        submit: i18next.t('global.search.form_input.search_button'),
      });
      u(document.body).append(templateWithDatePicker);
    }

    components = setupAutoCompleteTriggers();

    document.addEventListener('setupAutoCompleteTriggers', () => {
      clearCache();
      resetAutoComplete();
    });
  });
}

function resetAutoComplete() {
  if (reset.flag !== resetStatus.IDLE) {
    if (!reset.queue) {
      reset.queue = true;
    }

    return false;
  }

  reset.flag = resetStatus.PENDING;
  reset.queue = false;

  checkBuildStatus().then(() => {
    reset.flag = resetStatus.INPROGRESS;
    for (let i = 0; i < components.length; i++) {
      components[i].destroy();
    }

    components = setupAutoCompleteTriggers();
    reset.flag = resetStatus.IDLE;

    if (reset.queue) {
      resetAutoComplete();
    }
  });

  return true;
}

function checkBuildStatus() {
  let interval = null;
  let circle = 0;

  return new Promise((resolve, reject) => {
    interval = setInterval(() => {
      const suggests = store.getState().autoSuggest;
      let mounted = 0;

      if (typeof suggests === 'object' && suggests !== null) {
        Object.keys(suggests).map((id) => {
          if (suggests[id].buildStatus === buildStatus.MOUNTED) {
            mounted += 1;
          }

          return true;
        });

        if (Object.keys(suggests).length === mounted) {
          clearInterval(interval);
          resolve();
        }
      }

      circle += 1;

      if (circle > 3 && suggests === null) {
        resolve();
      }

      if (circle > 100) {
        clearInterval(interval);
        reject();
      }
    }, 500);
  });
}

function setupAutoCompleteTriggers() {
  const elements = document.querySelectorAll('.js-autocomplete');
  const lastSearch = getUserLastSearch();
  const instances = [];

  for (let i = 0; i < elements.length; i++) {
    const props = JSON.parse(elements[i].getAttribute('data-settings')) || {};

    if (props.enableDatePicker) {
      props.lastSearch = lastSearch;
    }

    if (isSmallScreen() && props.chooseBehavior !== 'fill' && props.enableModal !== false) {
      if (props.enableDatePicker) {
        instances.push(
          new AutoCompleteModalWithDatePickerComponent(elements[i], {
            i18n: i18next,
            store,
            ...props,
          })
        );
      } else {
        instances.push(
          new AutoCompleteModalComponent(elements[i], {
            i18n: i18next,
            store,
            ...props,
          })
        );
      }
    } else {
      instances.push(
        new AutoCompleteComponent(elements[i], i, {
          i18n: i18next,
          store,
          ...props,
        })
      );
    }
  }

  if (isSmallScreen()) {
    const modalElements = document.querySelectorAll('.js-autocomplete-modal');

    for (let i = 0; i < modalElements.length; i++) {
      const props = JSON.parse(modalElements[i].getAttribute('data-settings')) || {};

      if (props.enableDatePicker) {
        props.lastSearch = lastSearch;
      }

      if (props.enableDatePicker) {
        instances.push(
          new AutoCompleteModalWithDatePickerComponent(modalElements[i], {
            i18n: i18next,
            store,
            ...props,
          })
        );
      } else {
        instances.push(
          new AutoCompleteModalComponent(modalElements[i], {
            i18n: i18next,
            store,
            ...props,
          })
        );
      }
    }
  }

  for (let i = 0; i < instances.length; i++) {
    instances[i].init();
  }

  store.dispatch(actions.getPopularCitiesData());
  store.dispatch(actions.getRecentSearchData());

  return instances;
}

if (document.readyState !== 'loading') {
  onDocumentReady();
} else {
  document.addEventListener('DOMContentLoaded', onDocumentReady);
}



