import { combineReducers } from 'redux';
import { actionTypes } from '~components/date-picker/js/actions';

export const moveInTab = 'move-in';
export const moveOutTab = 'move-out';

const MONTHS_UPPER_BOUND = 30;
const PICKER_VALUES_DEFAULT_STATE = {
  moveOutTabEnabled: false,
  tab: null,
  moveInMonth: '',
  moveInMonthString: '',
  moveOutMonth: '',
  moveOutMonthString: '',
  moveInMonthFormatted: '',
  moveOutMonthFormatted: '',
  reset: false,
};
const ENABLED_MONTHS_DEFAULT_STATE = {
  monthMapping: {},
  moveInYear: null,
  moveOutYear: null,
  moveInYearIncrementEnabled: false,
  moveInYearDecrementEnabled: false,
  moveOutYearIncrementEnabled: false,
  moveOutYearDecrementEnabled: false,
  moveInAvailable: {},
  moveOutAvailable: {},
  isPreSelecting: false,
};

function getFormattedDate(month, monthString) {
  const year = month.slice(0, 4);
  return `${monthString} ${year}`;
}

function getNextAvailableYear(minYear, availableMonths) {
  const years = Object.keys(availableMonths).map(year => parseInt(year, 10));
  let nextAvailableYear = minYear;

  years.sort();
  outerLoop: // eslint-disable-line no-labels,no-restricted-syntax
    for (let i = 0; i < years.length; i++) {
      const year = years[i];
      if (year >= minYear) {
        for (let j = 0; j < availableMonths[year].length; j++) {
          const month = availableMonths[year][j];
          if (month.enabled) {
            nextAvailableYear = year;
            break outerLoop; // eslint-disable-line no-labels,no-restricted-syntax
          }
        }
      }
    }

  return nextAvailableYear;
}

function canIncrementYear(year, availableYears) {
  return year < Math.max.apply(null, availableYears);
}

function canDecrementYear(year, availableYears) {
  return year > Math.min.apply(null, availableYears);
}

function getIncrementAndDecrementEnabledStates(year, availableYears) {
  const years = Object.keys(availableYears).map(availableYear => parseInt(availableYear, 10));
  const incrementEnabled = canIncrementYear(year, years);
  const decrementEnabled = canDecrementYear(year, years);

  return {
    incrementEnabled,
    decrementEnabled,
  };
}

function generateMonths(inputDates, all, lowerBound) {
  const result = {};
  const today = new Date();
  const currentMonth = today.getMonth();
  let upperBound = MONTHS_UPPER_BOUND + currentMonth;
  const lastDate = new Date(today.getFullYear(), upperBound);
  const startDate = new Date(today.getFullYear(), 0);

  if (lastDate.getMonth() < 11) {
    upperBound += 11 - lastDate.getMonth();
  }

  for (let i = 0; i <= upperBound; i++) {
    let enabled = false;
    const currentDate = new Date(startDate.getFullYear(), startDate.getMonth() + i + 1, 0);
    const currentYear = currentDate.getFullYear();
    const formattedMonth = `0${currentDate.getMonth() + 1}`.slice(-2);
    const formattedDate = `${currentYear}-${formattedMonth}`;
    const isInRange = (!lowerBound && currentDate >= today) ||
     (lowerBound && formattedDate >= lowerBound);

    if (isInRange && (all === true || inputDates.indexOf(formattedDate) >= 0)) {
      enabled = true;
    }

    if (result[currentYear] === undefined) {
      result[currentYear] = [];
    }

    result[currentYear].push({
      value: formattedDate,
      enabled,
    });
  }

  return result;
}

function formatMonths(months) {
  const minYear = getMinYear(months);
  const maxYear = getMaxYear(months);
  const result = {};

  for (let i = minYear; i <= maxYear; i++) {
    result[i] = [];
    for (let j = 1; j <= 12; j++) {
      const month = `0${new Date(`${i}-${j}-1`).getMonth() + 1}`.slice(-2);
      const value = `${i}-${month}`;

      result[i].push({
        value,
        enabled: months.indexOf(value) !== -1,
      });
    }
  }

  return result;
}

function getMinYear(months) {
  let minYear = 9999;

  months.map((month) => {
    if (Number(month.slice(0, 4)) < minYear) {
      minYear = Number(month.slice(0, 4));
    }
    return true;
  });

  return minYear;
}

function getMaxYear(months) {
  let maxYear = 0;

  months.map((month) => {
    if (Number(month.slice(0, 4)) > maxYear) {
      maxYear = Number(month.slice(0, 4));
    }
    return true;
  });

  return maxYear;
}

export function monthsReducer(state = PICKER_VALUES_DEFAULT_STATE, action) {
  let newState = state;

  switch (action.type) {
    case actionTypes.DATE_PICKER_SELECT_MOVE_IN_MONTH: {
      const moveInMonth = action.payload.month;
      const formattedMoveInMonth = action.payload.monthString;

      newState = Object.assign({}, state, {
        moveOutTabEnabled: true,
        moveInMonth,
        moveInMonthString: formattedMoveInMonth,
        moveOutMonth: '',
        moveOutMonthFormatted: '',
        moveInMonthFormatted: getFormattedDate(moveInMonth, formattedMoveInMonth),
        reset: false,
      });
      break;
    } case actionTypes.DATE_PICKER_SELECT_MOVE_OUT_MONTH: {
      const moveOutMonth = action.payload.month;
      const formattedMoveOutMonth = action.payload.monthString;

      if (state.moveOutTabEnabled) {
        newState = Object.assign({}, state, {
          moveOutMonth,
          moveOutMonthString: formattedMoveOutMonth,
          moveOutMonthFormatted: getFormattedDate(moveOutMonth, formattedMoveOutMonth),
        });
      }
      break;
    } case actionTypes.DATE_PICKER_RESET_MONTHS: {
      newState = Object.assign({}, state, {
        moveOutTabEnabled: false,
        moveInMonth: '',
        moveInMonthString: '',
        moveOutMonth: '',
        moveOutMonthString: '',
        moveInMonthFormatted: '',
        moveOutMonthFormatted: '',
        reset: true,
      });
      break;
    } case actionTypes.DATE_PICKER_SELECT_TAB: {
      if (!state.moveOutTabEnabled && action.payload.tab === 'move-out') {
        break;
      }
      newState = Object.assign({}, state, {
        tab: action.payload.tab,
      });
      break;
    } default: {
      return state;
    }
  }

  return newState;
}

export function yearsReducer(state = ENABLED_MONTHS_DEFAULT_STATE, action) {
  let newState = state;

  switch (action.type) {
    case actionTypes.DATE_PICKER_INIT_MOVE_IN_YEARS: {
      const moveInYear = state.moveInYear !== null ? state.moveInYear : new Date().getFullYear();
      const useAllMonths = action.payload.monthMapping === 'all';
      const availableMonths = useAllMonths ? generateMonths(null, true) :
        formatMonths(Object.keys(action.payload.monthMapping));
      const incrementStates = getIncrementAndDecrementEnabledStates(moveInYear, availableMonths);

      newState = Object.assign({}, state, {
        monthMapping: action.payload.monthMapping,
        moveInYear,
        moveOutYear: state.moveOutYear !== null ? state.moveOutYear : moveInYear,
        moveInAvailable: availableMonths,
        moveInYearIncrementEnabled: incrementStates.incrementEnabled,
        moveInYearDecrementEnabled: incrementStates.decrementEnabled,
      });
      break;
    } case actionTypes.DATE_PICKER_INIT_MOVE_OUT_YEARS: {
      const moveInMonth = action.payload.moveInMonth;
      const useAll = state.monthMapping === 'all';
      const mappedMonths = state.monthMapping[moveInMonth];

      if (useAll || (mappedMonths !== undefined && mappedMonths.length)) {
        const availableMonths = useAll ? generateMonths(null, true, moveInMonth) :
          formatMonths(mappedMonths);
        let moveOutYear = state.moveOutYear;
        if (!state.isPreSelecting) {
          moveOutYear = getNextAvailableYear(state.moveInYear, availableMonths);
        }
        const incrementStates = getIncrementAndDecrementEnabledStates(moveOutYear, availableMonths);

        newState = Object.assign({}, state, {
          moveOutYear,
          moveOutAvailable: availableMonths,
          moveOutYearIncrementEnabled: incrementStates.incrementEnabled,
          moveOutYearDecrementEnabled: incrementStates.decrementEnabled,
        });
      }
      break;
    } case actionTypes.DATE_PICKER_RESET_YEARS: {
      const currYear = new Date().getFullYear();
      const incrementMoveInStates = getIncrementAndDecrementEnabledStates(
        currYear,
        state.moveInAvailable
      );
      const incrementMoveOutStates = getIncrementAndDecrementEnabledStates(
        currYear,
        state.moveOutAvailable
      );

      newState = Object.assign({}, state, {
        moveInYear: currYear,
        moveOutYear: currYear,
        moveInYearIncrementEnabled: incrementMoveInStates.incrementEnabled,
        moveInYearDecrementEnabled: incrementMoveInStates.decrementEnabled,
        moveOutYearIncrementEnabled: incrementMoveOutStates.incrementEnabled,
        moveOutYearDecrementEnabled: incrementMoveOutStates.decrementEnabled,
      });
      break;
    } case actionTypes.DATE_PICKER_INCREMENT_MOVE_IN_YEAR: {
      if (state.moveInYearIncrementEnabled) {
        const newMoveInYear = state.moveInYear + 1;
        const incrementStates = getIncrementAndDecrementEnabledStates(
          newMoveInYear,
          state.moveInAvailable
        );

        newState = Object.assign({}, state, {
          moveInYear: newMoveInYear,
          moveInYearIncrementEnabled: incrementStates.incrementEnabled,
          moveInYearDecrementEnabled: incrementStates.decrementEnabled,
        });
      }
      break;
    } case actionTypes.DATE_PICKER_DECREMENT_MOVE_IN_YEAR: {
      if (state.moveInYearDecrementEnabled) {
        const newMoveInYear = state.moveInYear - 1;
        const incrementStates = getIncrementAndDecrementEnabledStates(
          newMoveInYear,
          state.moveInAvailable
        );

        newState = Object.assign({}, state, {
          moveInYear: newMoveInYear,
          moveInYearIncrementEnabled: incrementStates.incrementEnabled,
          moveInYearDecrementEnabled: incrementStates.decrementEnabled,
        });
      }
      break;
    } case actionTypes.DATE_PICKER_INCREMENT_MOVE_OUT_YEAR: {
      if (state.moveOutYearIncrementEnabled) {
        const newMoveOutYear = state.moveOutYear + 1;
        const incrementStates = getIncrementAndDecrementEnabledStates(
          newMoveOutYear,
          state.moveOutAvailable
        );

        newState = Object.assign({}, state, {
          moveOutYear: newMoveOutYear,
          moveOutYearIncrementEnabled: incrementStates.incrementEnabled,
          moveOutYearDecrementEnabled: incrementStates.decrementEnabled,
        });
      }
      break;
    } case actionTypes.DATE_PICKER_DECREMENT_MOVE_OUT_YEAR: {
      if (state.moveOutYearDecrementEnabled) {
        const newMoveOutYear = state.moveOutYear - 1;
        const incrementStates = getIncrementAndDecrementEnabledStates(
          newMoveOutYear,
          state.moveOutAvailable
        );

        newState = Object.assign({}, state, {
          moveOutYear: newMoveOutYear,
          moveOutYearIncrementEnabled: incrementStates.incrementEnabled,
          moveOutYearDecrementEnabled: incrementStates.decrementEnabled,
        });
      }
      break;
    } case actionTypes.DATE_PICKER_SELECT_MOVE_IN_YEAR: {
      const newMoveInYear = action.payload.year;
      const incrementStates = getIncrementAndDecrementEnabledStates(
        newMoveInYear,
        state.moveInAvailable
      );

      newState = Object.assign({}, state, {
        moveInYear: newMoveInYear,
        moveInYearIncrementEnabled: incrementStates.incrementEnabled,
        moveInYearDecrementEnabled: incrementStates.decrementEnabled,
      });
      break;
    } case actionTypes.DATE_PICKER_SELECT_MOVE_OUT_YEAR: {
      const newMoveOutYear = action.payload.year;
      const incrementStates = getIncrementAndDecrementEnabledStates(
        newMoveOutYear,
        state.moveOutAvailable
      );

      newState = Object.assign({}, state, {
        moveOutYear: newMoveOutYear,
        moveOutYearIncrementEnabled: incrementStates.incrementEnabled,
        moveOutYearDecrementEnabled: incrementStates.decrementEnabled,
      });
      break;
    } case actionTypes.DATE_PICKER_SET_IS_PRE_SELECTING: {
      newState = Object.assign({}, state, {
        isPreSelecting: action.payload.isPreSelecting,
      });
      break;
    } default: {
      return state;
    }
  }
  return newState;
}

const datePickerReducer = combineReducers({
  months: monthsReducer,
  years: yearsReducer,
});

export default datePickerReducer;



