/**
 * Render a block n times
 * Usage:
 * {{# times 5 10 1 }}<span>{{ this }}</span>{{/ times }}
 */
export default function times(from = 0, to, incrementBy, block) {
  let result = '';

  for (let i = from; i <= to; i += incrementBy) {
    result += block.fn(i);
  }

  return result;
}



