import { actionTypes } from '~components/autocomplete/js/actions';
import { asyncStateTypes, buildStatus } from '~components/autocomplete/js/constants';

export const defaultState = {
  status: asyncStateTypes.IDLE,
  buildStatus: buildStatus.CONSTRUCTING,
  payload: null,
};

export default function autoSuggest(state = null, action) {
  switch (action.type) {
    case actionTypes.AUTOCOMPLETE_REGISTER_COMPONENT:
    case actionTypes.AUTOCOMPLETE_RESET_AUTO_SUGGEST_DATA:
      return Object.assign({}, state, {
        [action.id]: defaultState,
      });

    case actionTypes.AUTOCOMPLETE_DESTROY_COMPONENT:
      delete state[action.id]; // eslint-disable-line no-param-reassign
      return Object.assign({}, state);

    case actionTypes.AUTOCOMPLETE_SET_AUTO_SUGGEST_DATA:
      if (typeof state[action.id] === 'undefined') {
        return state;
      }

      return Object.assign({}, state, {
        [action.id]: Object.assign({}, state[action.id], {
          status: asyncStateTypes.IDLE,
          payload: action.payload,
        }),
      });

    case actionTypes.AUTOCOMPLETE_FETCHING_DATA:
      return Object.assign({}, state, {
        [action.id]: Object.assign({}, state[action.id], {
          status: asyncStateTypes.FETCHING,
        }),
      });

    case actionTypes.AUTOCOMPLETE_COMPONENT_MOUNTED:
      return Object.assign({}, state, {
        [action.id]: Object.assign({}, state[action.id], {
          buildStatus: buildStatus.MOUNTED,
        }),
      });

    default:
      break;
  }

  return state;
}




