import { actionTypes } from '~components/autocomplete/js/actions';
import formatData from '~components/autocomplete/js/helpers';
import { setItem, removeItem } from '~global-js/helpers/storage';
import { getLocale } from '~global-js/helpers/locale';
import { localStorageKeys, localStorageVersion } from '~components/autocomplete/js/constants';

export const defaultState = {
  popularCities: [],
  popularSearch: [],
  recentSearch: [],
  datePicker: {
    moveInMonth: null,
    moveOutMonth: null,
  },
};

const locale = getLocale();

export default function preSuggest(state = defaultState, action) {
  switch (action.type) {
    case actionTypes.AUTOCOMPLETE_SET_POPULAR_CITIES_DATA: {
      let popularCities = action.payload;
      if (!action.fromCache) {
        popularCities = formatData(action.payload, 'popularCities');
        setItem(localStorageKeys.popularCities, compileLocalStageData(popularCities));
      }

      return Object.assign({}, state, {
        popularCities,
      });
    }

    case actionTypes.AUTOCOMPLETE_SET_RECENT_SEARCH_DATA: {
      if (action.reset === true) {
        removeItem(localStorageKeys.recentSearch);

        return Object.assign({}, state, {
          recentSearch: [],
        });
      }

      return Object.assign({}, state, {
        recentSearch: action.payload,
      });
    }

    case actionTypes.AUTOCOMPLETE_UPDATE_RECENT_SEARCH_DATA: {
      const newSearch = formatData([action.payload], 'popularCities')[0];

      const recentSearch = state.recentSearch.filter(value =>
        value.attributes !== newSearch.attributes
      );

      recentSearch.unshift(newSearch);

      if (recentSearch.length > 5) {
        recentSearch.pop();
      }

      setItem(localStorageKeys.recentSearch, compileLocalStageData(recentSearch));
      return Object.assign({}, state, {
        recentSearch,
      });
    }

    case actionTypes.AUTOCOMPLETE_UPDATE_DATE_PICKER: {
      return Object.assign({}, state, {
        datePicker: {
          moveInMonth: action.payload.moveInMonth,
          moveOutMonth: action.payload.moveOutMonth,
        },
      });
    }

    default:
      break;
  }

  return state;
}

function compileLocalStageData(payload) {
  return {
    createTime: new Date().getTime(),
    version: localStorageVersion,
    locale,
    payload,
  };
}



