import { u } from 'umbrellajs/umbrella';
import document from 'global/document';
import window from 'global/window';
import BaseComponent from '~global-js/component/base-component';
import keyboard from '~global/variables/keyboard.json';
import listTemplate from '~components/autocomplete/templates/list.hbs';
import { event } from '~global-js/helpers/tracking';
import { isLargeScreen } from '~global-js/helpers/breakpoints';
import processPersistUserLocalStorageData from '~global-js/helpers/persist-user-info';
import * as actions from '~components/autocomplete/js/actions';
import { getLocale } from '~global-js/helpers/locale';

export default class AutoCompleteInteraction extends BaseComponent {
  constructor(element, form, suggestionBox, props = {}) {
    super(element, props);

    this.state = {
      resultsList: suggestionBox.find('.autocomplete--search-results').first(),
      form,
      activeItem: 0,
      enabledClickListener: false,
      noPreFill: false,
      isAutoOpenDatePicker: false,
    };

    this.suggestionBox = suggestionBox;
  }

  init() {
    this.addEventListener();

    if (this.state.resultsList) {
      this.autoMatchItem();
      this.setActiveItemClass();
    }
  }

  isDatePickerAutoOpen = () => this.state.isAutoOpenDatePicker;

  update = (element, suggestionBox, noPreFill) => {
    this.destroy();
    this.state = Object.assign(this.state, {
      resultsList: suggestionBox.find('.autocomplete--search-results').first(),
      activeItem: 0,
      enabledClickListener: false,
    });

    if (noPreFill) {
      this.state.noPreFill = true;
    } else {
      this.state.noPreFill = false;
    }

    this.init();
  };

  addEventListener = () => {
    if (this.state.resultsList && !this.state.enabledClickListener) {
      this.element.addEventListener('keydown', this.handleUserKeyDown);
    }

    if (!this.state.enabledClickListener) {
      u(this.suggestionBox).find('a').on('click', this.handleUserClick);
      this.state.enabledClickListener = true;
    }

    if (this.state.form) {
      this.state.form.addEventListener('submit', this.handleUserSubmit);
    }
  };

  autoMatchItem = () => {
    const activeItemDetail = this.getActiveItemDetail();

    if (
      isLargeScreen()
      && !this.element.getAttribute('data-detail')
      && activeItemDetail.name.indexOf(this.element.value) === 0 &&
      !this.state.noPreFill
      ) {
      const startRange = this.element.value.length;

      this.setElementValue(activeItemDetail);
      if (document.activeElement === this.element) {
        this.element.setSelectionRange(startRange, activeItemDetail.name.length);
      }
    } else {
      const items = u(this.state.resultsList).find('li');
      items.nodes.map((item, index) => {
        if (this.element.getAttribute('data-detail') === item.getAttribute('data-detail')) {
          this.state.activeItem = index;
        } else if (
          !this.element.getAttribute('data-detail') &&
          item.querySelector('.autocomplete__text__primary').innerText === this.element.value &&
          this.state.activeItem === 0
        ) {
          this.state.activeItem = index;

          const detail = this.getActiveItemDetail();
          this.setElementValue(detail);
        }
        return true;
      });
    }
  };

  setActiveItemClass = () => {
    const items = u(this.state.resultsList).find('li');
    const activeItem = items.nodes[this.state.activeItem];

    items.removeClass('autocomplete__item--active');
    u(activeItem).addClass('autocomplete__item--active');
  };

  handleUserKeyDown = (e) => {
    switch (e.keyCode) {
      case keyboard.arrowDown:
        e.preventDefault();
        this.updateActiveItem('ADD');
        break;
      case keyboard.arrowUp:
        e.preventDefault();
        this.updateActiveItem('MINUS');
        break;
      case keyboard.tab:
        e.preventDefault();
        this.selectDatePicker();
        break;
      default:
        break;
    }
  };

  handleUserClick = (e) => {
    e.preventDefault();
    const clickedItem = u(e.target).closest('li').first();
    let link = '';
    let clickType = '';

    if (e.target.nodeName === 'A') {
      link = e.target.href;
    } else {
      const linkElement = u(e.target).closest('a').first();
      if (linkElement) {
        link = linkElement.href;
        if (linkElement.getAttribute('data-event')) {
          clickType = JSON.parse(linkElement.getAttribute('data-event')).clickType;
        }
      }
    }

    const index = JSON.parse(clickedItem.getAttribute('data-index'));
    const selectedItemDetail = JSON.parse(clickedItem.getAttribute('data-detail'));

    this.state.activeItem = index;
    this.setActiveItemClass();

    // If chooseBehavior not fill and search input doesn't have value
    // Then it comes from popular city/search recent/searches, just go to the current page.
    if (this.props.chooseBehavior === 'fill' && this.element.value && this.element.value !== '') {
      this.handlePreFill(clickedItem);
    } else if (this.props.enableDatePicker) {
      this.handleRedirectWithDatePicker({
        link,
        clickType,
        activeItemDetail: selectedItemDetail,
      });
    } else {
      this.props.store.dispatch(actions.updateRecentSearchData(selectedItemDetail));
      processPersistUserLocalStorageData(null, {
        lastSearch: selectedItemDetail.name,
      });

      window.location.href = link;
    }
  };

  handleUserSubmit = (e) => {
    e.preventDefault();

    if (!this.element.value || !this.element.getAttribute('data-detail')) {
      if (this.props.enableDatePicker) {
        this.element.focus();
      }
      return false;
    }

    if (!this.state.resultsList) {
      if (this.props.showNoResultHint) {
        const template = listTemplate({
          noResult: true,
          noResultText: this.props.i18n.t('global.search.no_result.label.not_recognise'),
        });
        this.suggestionBox.html(template);
        this.suggestionBox.addClass('autocomplete--active');
      }

      if (this.props.enableDatePicker) {
        this.element.focus();
      }
      return true;
    }

    if (document.activeElement !== this.element && this.props.enableDatePicker) {
      this.handleRedirectWithDatePicker({
        clickType: 'Execute',
      });
    } else if (this.props.chooseBehavior === 'fill') {
      this.handlePreFill(this.getActiveItem());
    } else {
      const activeItem = this.getActiveItem();
      const link = activeItem.querySelector('a').getAttribute('href');
      const selectedItemDetail = JSON.parse(activeItem.getAttribute('data-detail'));
      // without date picker submit
      event({
        c: this.props.searchPlacement,
        a: 'click',
        l: 'search > searchExecution:Execute',
        mp: [
          {
            type: 'track',
            key: 'Searched',
            params: {
              'Search Method': 'Auto Suggestion',
              'Search Page Type': selectedItemDetail.type === 'Area' ? 'null' : selectedItemDetail.type,
              'Search Slug': selectedItemDetail.slug,
              'Search Placement': this.props.searchPlacement,
              'Date Status': this.getDateStatus(),
              'Final Touch': 'Button',
              'AB Testing Variant': 'Old Version',
            },
          },
        ],
      });

      this.props.store.dispatch(actions.updateRecentSearchData(selectedItemDetail));
      processPersistUserLocalStorageData(null, {
        lastSearch: selectedItemDetail.name,
      });

      window.location.href = link;
    }

    return true;
  };

  getFinalTouch = (clickType, moveOutMonth) => {
    if (clickType === 'Popular city') {
      return 'Recommend';
    } else if (clickType === 'Recent search') {
      return 'History';
    } else if (clickType === 'Execute') {
      return 'Button';
    } else if (clickType === 'Auto') {
      return 'Auto Suggestion';
    } else if (!moveOutMonth) {
      return 'Not Certain';
    }

    return 'Move Out';
  };

  getDateStatus = () => {
    const state = this.props.store.getState();
    const moveInMonth = state.preSuggest.datePicker.moveInMonth;
    const moveOutMonth = state.preSuggest.datePicker.moveOutMonth;
    let dateStatus = 'Without Both';

    if (moveOutMonth) {
      dateStatus = 'With Both';
    } else if (moveInMonth) {
      dateStatus = 'With Move In';
    }

    return dateStatus;
  }

  handleRedirectWithDatePicker = ({ link = null, activeItemDetail = null, clickType = 'Click' }) => {
    const state = this.props.store.getState();
    const moveInMonth = state.preSuggest.datePicker.moveInMonth;
    const moveOutMonth = state.preSuggest.datePicker.moveOutMonth;
    const detail = activeItemDetail || JSON.parse(this.element.getAttribute('data-detail'));
    let url = link || detail.link;
    let searchMethod = 'Auto Suggestion';

    if (clickType === 'Popular city') {
      searchMethod = 'Recommend';
    } else if (clickType === 'Recent search') {
      searchMethod = 'History';
    }

    if (moveInMonth) {
      url = `${url}?check_in_date=${moveInMonth}`;
    }

    if (moveOutMonth) {
      url = `${url}&check_out_date=${moveOutMonth}`;
    }

    processPersistUserLocalStorageData(null, {
      lastSearch: detail.name,
      targetMoveInMonth: moveInMonth,
      targetMoveOutMonth: moveOutMonth,
    });

    this.props.store.dispatch(actions.updateRecentSearchData(detail));

    // Below are tracking for GA/MP
    event({
      mp: [{
        type: 'track',
        key: 'Searched',
        params: {
          'Search Method': searchMethod,
          'Search Page Type': detail.type === 'Area' ? 'null' : detail.type,
          'Search Slug': detail.slug,
          'Search Placement': this.props.searchPlacement,
          'Date Status': this.getDateStatus(),
          'Final Touch': this.getFinalTouch(clickType, moveOutMonth),
          'AB Testing Variant': 'Old Version',
        },
      }],
    });

    if (clickType === 'Auto' || clickType === 'Execute') {
      event({
        c: this.props.searchPlacement,
        a: 'click',
        l: `search > searchExecution:${clickType}`,
      });
    } else {
      event({
        c: this.props.searchPlacement,
        a: 'click',
        l: `search > ${clickType}:${detail.slug}`,
      });
    }

    window.location.href = url;
  };

  handlePreFill = (item) => {
    const detail = JSON.parse(u(item).data('detail'));
    this.setElementValue(detail);
    this.element.setAttribute('data-detail', JSON.stringify(detail));

    this.suggestionBox.removeClass('autocomplete--active');

    // Select one search result with datepicker search
    if (this.props.enableDatePicker &&
        !this.props.store.getState().preSuggest.datePicker.moveInMonth) {
      this.state.isAutoOpenDatePicker = true;
      setTimeout(this.selectDatePicker, 100);
    }

    const preFillEvent = document.createEvent('CustomEvent');
    preFillEvent.initCustomEvent('autocompletePreFilled', true, false, detail);
    this.element.dispatchEvent(preFillEvent);
  };

  getActiveItem = () => u(this.state.resultsList).find('li').nodes[this.state.activeItem];

  getActiveItemDetail = () => JSON.parse(u(this.getActiveItem()).data('detail'));

  updateActiveItem = (method) => {
    const listLength = u(this.state.resultsList).find('li').nodes.length;

    if (method === 'ADD' && (this.state.activeItem < listLength - 1)) {
      this.state.activeItem = this.state.activeItem + 1;
    }

    if (method === 'MINUS' && (this.state.activeItem !== 0)) {
      this.state.activeItem = this.state.activeItem - 1;
    }

    this.setElementValue(this.getActiveItemDetail());
    this.setActiveItemClass();
  };

  setElementValue = (detail) => {
    if (getLocale() === 'zh-cn') {
      this.element.value = detail.name;
    } else {
      this.element.value = detail.original_name;
    }

    this.element.setAttribute('data-value', detail.slug);
    this.element.setAttribute('data-detail', JSON.stringify(detail));
  };

  selectDatePicker = () => {
    if (this.props.enableDatePicker) {
      this.state.form.querySelector('.date-picker__tab').click();
    }

    this.state.isAutoOpenDatePicker = false;
  };

  destroy() {
    if (this.state.resultsList) {
      this.element.removeEventListener('keydown', this.handleUserKeyDown);
    }

    if (this.state.form) {
      this.state.form.removeEventListener('submit', this.handleUserSubmit);
    }
  }

  static defaultProps = {
    chooseBehavior: 'redirect',
    searchPlacement: null,
  }
}



